.sidebar {
	position: fixed;
	left: 0;
	top: 40px;
	width: 100%;
	height: calc(100% - 40px);
	z-index: 10;
	visibility: hidden;
	transition: opacity 0.5s ease, visibility 0.5s ease;

	&__overlay {
		opacity: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-color: rgba($black, 0.5);
		transition: opacity 0.5s ease; }

	&__content {
		position: absolute;
		top: 0;
		left: -300px;
		display: flex;
		flex-direction: column;
		padding: 30px;
		width: 300px;
		height: 100%;
		z-index: 5;
		background-color: $white;
		transition: left 0.5s ease; }

	&__btn {
		margin-bottom: 20px;
		font-size: 16px;
		&--hide {
			display: none;
			&.is-show {
				display: block; } } }
	&__link {
		margin-top: auto;
		@include fontRB;
		font-size: 20px;
		color: $black;
		text-transform: uppercase;
		text-align: right; }

	&.is-show {
		opacity: 1;
		visibility: visible;

		& .sidebar__content {
			left: 0; }
		& .sidebar__overlay {
			opacity: 1; } } }
