.root[data-section="auth"] {

	.user-block {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin-bottom: 30px;

		&__content {
			display: flex;
			justify-content: space-between;
			flex-direction: row;
			width: 100%; }

		&__room {
			display: none; }
		&__household {
			display: none; }

		&__btn {
			width: 165px;
			height: 50px;
			margin: 0;
			&[data="save"] {
				display: none; }
			&[data="census"] {
				display: flex; }
			&[data="info"] {
				display: flex; }
			&[data="send"] {
				display: none; } }

		&__title {
			display: block;
			@include vcenter;
			@include fontRB;
			font-size: 36px;
			color: $blue;
			text-transform: uppercase; }

		&__overlay {
			display: none; } } }

.user-block {
	position: absolute;
	display: flex;
	flex-direction: column;
	margin-bottom: 30px;

	&__content {
		display: flex;
		flex-direction: column; }

	&__overlay {
		display: none; }

	&__btn {
		width: 165px;
		height: 50px;
		margin-bottom: 20px;
		&[data="census"] {
			display: none; }
		&[data="info"] {
			display: none; } }

	&__title {
		display: none;
		@include vcenter;
		@include fontRB;
		font-size: 36px;
		color: $blue;
		text-transform: uppercase; }

	&__room {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 165px;
		height: 165px;
		padding: 10px 10px 20px;
		margin-bottom: 10px;
		background-color: $white;
		// border: 1px solid $blue
		border-radius: 5px;
		text-align: center;
		transition: all 0.2s ease;
		box-shadow: 0 4px 22px rgba(0,0,0, 0.25), inset 0 3px 0 rgba(255,255,255, 0.5), 0 3px 0 rgba(0,0,0, 0.1), inset 0 0 22px rgba(0,0,0,0.1);
		&-title {
			@include fontRM;
			font-size: 18px;
			text-transform: uppercase; }
		&-text {
			@include fontRR;
			font-size: 15px;
			overflow: hidden;
			max-width: 100%;
			max-height: 80px; }
		&:hover {
			transform: scale(1.05); } }

	&__household {
		// display: none
		align-items: center;
		justify-content: center;
		height: 50px;
		padding: 3px 10px;
		width: 165px;
		margin-bottom: 10px;
		background-color: $white;
		// border: 1px solid $blue
		border-radius: 5px;
		text-align: center;
		transition: all 0.2s ease;
		box-shadow: 0 4px 22px rgba(0,0,0, 0.25), inset 0 3px 0 rgba(255,255,255, 0.5), 0 3px 0 rgba(0,0,0, 0.1), inset 0 0 22px rgba(0,0,0,0.1);
		overflow: hidden;
		&-text {
			@include fontRB;
			font-size: 14px; }
		&:hover {
			transform: scale(1.05); } } }
.root[data-section="s_member"] {
	.user-block {
		margin-bottom: 0;
		&__btn {
			margin-bottom: 10px; }
		&__room {
			position: static;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 50px;
			padding: 0;
			overflow: hidden;
			&-title {
				@include fontRB;
				font-size: 14px; }
			&-text {
				display: none; } }
		&__household {
			display: flex; }

		.member {
			margin: 0 auto;
			pointer-events: none;
			.line-pair, .line {
				display: none; }
			&:after, &:before {
				display: none; }
			&__close {
				display: none; }
			&__valid {
				display: none; } } } }

.root[data-section="family"] {
	.user-block {
		&__household {
			box-shadow: inset 0 0 22px rgba(0,0,0,0.2), 0 3px 0 rgba(255,255,255,0.4),inset 0 3px 0 rgba(0,0,0,0.1), inset 0 0 22px rgba(0,0,0,0.1);
			cursor: initial;
			pointer-events: none;
			&:hover {
				transform: scale(1); } } } }

.root[data-section="room"] {
	.user-block {
		&__room {
			box-shadow: inset 0 0 22px rgba(0,0,0,0.2), 0 3px 0 rgba(255,255,255,0.4),inset 0 3px 0 rgba(0,0,0,0.1), inset 0 0 22px rgba(0,0,0,0.1);
			cursor: initial;
			// pointer-events: none
			&:hover {
				transform: scale(1); } } } }

.root[data-section="indiv"] {

	.user-block {
		display: none; } }

body.android {

	.user-block {
		position: fixed;
		left: 0;
		top: 40px;
		width: 100%;
		height: calc(100% - 40px);
		z-index: 11;
		visibility: hidden;
		transition: opacity 0.5s ease, visibility 0.5s ease;

		&__link {
			margin-top: auto;
			@include fontRB;
			font-size: 20px;
			color: $black;
			text-transform: uppercase;
			text-align: right; }

		&__content {
			position: absolute;
			top: 0;
			left: -270px;
			display: flex;
			flex-direction: column;
			padding: 30px 52px;
			width: 270px;
			height: 100%;
			z-index: 5;
			background-color: $white;
			transition: left 0.5s ease; }

		&__overlay {
			display: block;
			opacity: 0;
			z-index: -1;
			width: 100%;
			height: 100%;
			background-color: rgba($black, 0.5);
			transition: opacity 0.5s ease; }

		&__btn[data="send"] {
			display: none; }

		&__room {
			height: 50px;
			flex-direction: initial;
			padding: 0;
			&-title {
				@include fontRB;
				font-size: 14px;
				width: 100%; }
			&-text {
				display: none; } }

		.member {
			display: none; }

		&.is-show {
			opacity: 1;
			visibility: visible;

			& .user-block__content {
				left: 0; }
			& .user-block__overlay {
				opacity: 1; } } } }
