.choices {
	width: 100%;

	&__inner {
		display: flex;
		align-items: center;
		width: 100%;
		height: 40px;
		padding: 0 30px;
		@include fontRR;
		font-size: 18px;
		line-height: 40px;
		color: $black;
		background-color: $lightGray;
		border: 1px solid $blue;
		border-radius: 0; }

	&__input {
		min-width: 380px!important;
		margin: 0;
		@include placeholder {
			@include fontRR;
			font-size: 18px;
			color: $gray;
			font-style: italic; } }

	&__placeholder {
		opacity: 1;
		color: $gray;
		font-style: italic; }

	&__list--dropdown {
		position: absolute;
		top: 40px;
		left: 0;
		input {
			height: 40px; }
		.choices__placeholder {
			display: none; }
		.choices__list {
			max-height: 300px; } }

	&__list--multiple {
		display: flex;
		.choices__item[data-deletable] {
			padding: 0 10px; }
		.choices__item {
			flex-shrink: 0;
			height: 24px;
			line-height: 24px;
			@include fontRR;
			margin: 0 10px 0 0;
			border-radius: 0;
			border: none;
			background-color: $blue; } }

	&[data-type*=select-one]:before, &[data-type*=select-multiple]:before {
		content: '';
		position: absolute;
		right: 0;
		top: 0;
		width: 40px;
		height: 40px;
		background-color: $blue;
		border: none;
		margin: 0;
		padding: 0; }

	&[data-type*=select-one]:after, &[data-type*=select-multiple]:after {
		content: '';
		position: absolute;
		right: 12px;
		top: 11px;
		width: 14px;
		height: 14px;
		background-color: $transp;
		border: none;
		border-right: 4px solid $white;
		border-bottom: 4px solid $white;
		transform: rotate(45deg); }

	&.is-open {
		&[data-type*=select-one]:after, &[data-type*=select-multiple]:after {
			margin: 0;
			border-color: $white;
			top: 15px;
			transform: rotate(225deg); }

		.choices__list--dropdown {
			border-color: $blue; } }

	&.is-focused .choices__inner {
		border-color: $blue; }

	&.is-disabled {
		opacity: 0.5;
		.choices__inner {
			background-color: $lightGray;
			border-color: $gray; } }
	&.is-disabled[data-type*=select-one]:before, &.is-disabled[data-type*=select-multiple]:before {
		cursor: no-drop; } }
