.form-household-count {
	$this: &;
	margin-bottom: 30px;
	display: flex;
	width: 945px;
	height: 850px;
	margin-left: auto;
	background-color: $white;
	box-shadow: 0px 4px 12px rgba($black, 0.25);
	border-radius: 5px 5px 5px 0;

	&__scroll {
		width: 950px;
		height: 350px;
		padding: 30px 0;
		overflow-y: scroll;
		display: flex;
		transform: translateX(-55px);
		transition: height 0.2s ease;
		&.is-small {
			height: 200px; }

		&-content {
			flex-grow: 1;
			padding: 0 50px; }

		#{$this}__flex {
			margin: 10px 0;
			padding: 10px 0; } }

	&__wrap {
		padding: 50px;
		max-width: 100%; }

	&__wrap-scroll {
		overflow-y: scroll;
		height: 99%;
		padding-bottom: 40px; }

	&__flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
		&--center {
			justify-content: center;
			margin: 0 auto; }
		&--gray {
			background-color: $gray2; }
		&--column {
			flex-direction: column;
			align-items: flex-start; }
		&--hard-height {
			height: 80px; } }

	&__el {
		display: flex;
		justify-content: space-between;
		align-items: center;
		&-title {
			min-width: 90px;
			@include fontRM;
			font-size: 18px;
			text-transform: uppercase; } }

	&__col {
		width: 48%;
		.input {
			width: 250px; } }

	&__subtitle {
		@include fontRB;
		font-size: 24px;
		text-align: center;
		margin-bottom: 30px;
		&--no-mb {
			margin-bottom: 0; } }

	&__btn {
		margin-left: 10px;
		transform: translateY(3px);
		background-color: $transp;
		transition: visibility 0.2s ease, opacity 0.2s ease;
		svg {
			font-size: 16px;
			fill: $blue; }
		&-open {
			visibility: hidden;
			opacity: 0;
			animation: arrow-house 0.8s linear infinite; }
		&-close {
			transform: rotate(180deg);
			animation: arrow-house-left 0.8s linear infinite; }
		&.is-show {
			visibility: visible;
			opacity: 1; }
		&:hover {
			animation: none; } }

	&__info {
		@include fontRR;
		font-size: 16px;
		color: $blue;
		font-style: italic;
		&-more {
			visibility: hidden;
			opacity: 0;
			transition: visibility 0.2s ease, opacity 0.2s ease; }
		&--border {
			height: 120px;
			font-size: 16px;
			padding: 20px 0;
			margin: 30px 0;
			border-top: 1px solid $blue;
			border-bottom: 1px solid $blue;
			transition: height 0.3s ease;
			&.is-full {
				height: 280px;
				.form-household-count__info-more {
					visibility: visible;
					opacity: 1; } } }
		&--padd {
			padding: 0 30px; }
		&--center {
			text-align: center; }
		&--mb {
			margin-bottom: 15px; } }

	&__popup {
		position: relative;
		padding: 30px 0;
		height: 100%;
		#{$this}__flex {
			padding: 15px 30px; }
		#{$this}__subtitle {
			margin: 0 auto 10px;
			font-size: 18px; }
		#{$this}__el {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0; } }
		#{$this}__col {
			width: auto;
			margin-right: 30px;
			&:last-child {
				margin-right: 0; } } }
	&__icon {
		position: absolute;
		top: 15px;
		right: 15px;
		display: flex;
		width: 30px;
		height: 30px;
		cursor: pointer;
		svg {
			width: 100%;
			height: 100%; } }

	&__title {
		@include fontRB;
		font-size: 24px;
		text-align: center;
		text-transform: uppercase; }

	&__btns {
		display: flex;
		justify-content: space-between;
		padding: 0 30px;
		button {
			width: 330px; } }

	&__nav-title {
		position: relative;
		width: 205px;
		height: 100%;
		margin: 0;
		padding-left: 10px;
		list-style-type: none;
		li {
			position: relative;
			display: block;
			width: 100%;
			height: 80px;
			margin: 10px 0;
			@include fontRM;
			font-size: 16px;
			line-height: 80px;
			text-align: center;
			border-right: 1px solid $white;
			background-color: $blue;
			border-radius: 5px 0 0 5px;
			transition: all 0.2s ease;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				height: 80px;
				width: 900px;
				background-color: rgba($blue, 0.05);
				border-radius: 5px;
				transition: all 0.2s ease;
				z-index: -1; }
			a {
				display: block;
				width: 100%;
				height: 100%;
				color: $white;
				border-radius: 5px 0 0 5px; }
			&.is-active {
				background-color: $white;
				border-radius: 5px 0 0 5px;
				a {
					color: $black; }
				&:before {
					background-color: $white;
					box-shadow: 0 0 10px 3px rgba($black, 0.4); }
				&:hover {
					background-color: $white; } }
			&:hover {
				background-color: $blueHover;
				&:before {
					box-shadow: 0 0 10px 3px rgba($black, 0.4); } } } } }


@keyframes arrow-house {
	0% {
		transform: translate(0, 3px); }
	50% {
		transform: translate(6px, 3px); }
	100% {
		transform: translate(0, 3px); } }

@keyframes arrow-house-left {
	0% {
		transform: translate(0, 0) rotate(180deg); }
	50% {
		transform: translate(-6px, 0) rotate(180deg); }
	100% {
		transform: translate(0, 0) rotate(180deg); } }
