.form-create-member {
	$this: &;
	display: none;
	position: fixed;
	overflow-y: scroll;
	padding: 100px 0;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba($black, 0.5);
	z-index: 20;
	&.is-show {
		display: block; }
	& *.is-disabled {
		pointer-events: none; }
	&__content {
		// +vcenter
		position: relative;
		margin: 0 auto;
		width: 800px;
		padding: 30px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; }

	&__wrap {
		display: flex;
		justify-content: center;
		margin-bottom: 25px; }

	&__flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 25px;
		&.is-disabled {
			opacity: 0.7;
			#{$this}__subtitle {
				color: $gray; }
			#{$this}__el--children {
				color: $gray;
				border: 1px solid $gray;
				font-style: italic; } } }

	&__el {
		flex-grow: 1;
		max-width: 486px;
		&--children {
			position: relative;
			max-width: 486px;
			height: 40px;
			background-image: linear-gradient( 45deg, transparent 50%, $gray 50% ), linear-gradient( 135deg, $gray 50%, transparent 50% );
			background-position: calc( 100% - 20px ) 50%, calc( 100% - 15px ) 50%;
			background-size: 5px 5px, 5px 5px;
			background-repeat: no-repeat;
			& > span {
				position: absolute;
				left: 30px;
				top: 50%;
				transform: translateY(-50%);
				font-size: 18px; }
			// &:after
			// 	content: ''
			// 	position: absolute
			// 	top: 0
			// 	right: 0
			// 	width: 0
			// 	border: 5px solid black

			.choices {
				font-style: normal;
				color: $black;
				&.is-focused .choices__inner {
					border-color: $gray; }
				&.is-open {
					.choices__list--dropdown {
						border-color: $gray; }
					&[data-type*=select-multiple]:after {
						top: 18px; } } }
			.choices__input {
				min-width: 100px!important; }
			.choices__inner {
				border-color: $gray;
				background-color: $white;
				input {
					background-color: $white; } }
			.choices[data-type*=select-multiple]:before {
				background-color: $transp; }
			.choices[data-type*=select-multiple]:after {
				top: 14px;
				right: 18px;
				width: 0;
				height: 0;
				border: 3px solid black;
				border-top-color: $transp;
				border-left-color: $transp; } } }

	&__inner {
		flex-grow: 1;
		display: flex;
		justify-content: space-between; }

	&__date {
		flex-grow: 1;
		margin-right: 10px;
		&:last-child {
			margin-right: 0; } }

	&__age {
		@include fontRB;
		font-size: 18px; }

	&__subtitle {
		width: 240px;
		@include fontRM;
		font-size: 18px;
		text-transform: uppercase;
		margin-right: 10px;
		&--center {
			text-align: center;
			margin: 0 auto; } }

	&__radio {
		margin-right: 130px;
		&:last-child {
			margin-right: 0; } }

	&__btns {
		display: flex;
		justify-content: space-between; }

	&__btn {
		width: 230px; }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }

	.input:disabled {
		opacity: 1;
		color: $gray; }
	.input::-webkit-inner-spin-button,
	.input::-webkit-outer-spin-button {
		-webkit-appearance: none; } }
