.form-member {
	$this: &;

	&__inner {
		padding: 30px;
		height: 100%; }

	&__flex {
		position: relative;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		&--column {
			flex-direction: column;
			height: 100%;
			max-height: 95%; }
		&--center {
			align-items: center; }
		&--start {
			justify-content: flex-start; }
		&--space-around {
			justify-content: space-around;
			margin-bottom: 55px; }
		&--auto {
			height: auto; }
		&.is-disabled {
			opacity: 0.5;
			pointer-events: none; } }

	&__wrap {
		margin-left: 30px; }

	&__row {
		position: relative;
		&--mb {
			margin-bottom: 20px; }
		&--mb-big {
			margin-bottom: 40px; }
		&.is-disabled {
			opacity: 0.5; } }

	&__col {
		position: relative;
		width: 48.5%;
		margin-bottom: 20px;
		$col: &;
		&.is-disabled {
			opacity: 0.5; }
		&--absolute {
			position: absolute;
			bottom: -100px;
			right: 0;
			width: 48.5%;
			border: 1px solid $blue;
			#{$col} {
				width: 100%; }
			&-a8 {
				border: none;
				width: calc(100% - 25px);
				bottom: -310px;
				#{$this}__wrap {
					margin-bottom: 10px; } } }

		&--mb-big {
			margin-bottom: 40px; }
		&--large {
			width: 90%;
			margin-left: auto; }
		&--medium {
			width: 70%; }
		&--medium-right {
			width: 70%;
			margin-left: auto; }
		&--mb-small {
			margin-bottom: 5px; }
		&--mt-negative {
			margin-top: -60px;
			.input {
				margin-top: 15px; } } }

	&__el {
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0; }
		&--mb-big {
			margin-bottom: 40px; }
		&--mb-medium {
			margin-bottom: 15px; }
		&--absolute {
			position: absolute;
			left: 50px;
			top: 200px; }

		.radio + .form-member__input {
			margin-top: 15px; }

		&.is-disabled {
			opacity: 0.5;
			pointer-events: none; } }

	&__title {
		width: 100%;
		@include fontRB;
		font-size: 22px;
		text-align: center;
		margin-bottom: 30px;
		&--left {
			text-align: left; }
		&--medium {
			max-width: 650px;
			margin: 0 auto 20px; }
		&--no-mb {
			margin-bottom: 0; } }

	&__subtitle {
		display: flex;
		align-items: center;
		height: 50px;
		font-size: 16px;
		padding: 0 20px;
		margin-bottom: 20px;
		@include fontRM;
		background-color: $lightBlue;
		&--transp {
			background-color: $transp;
			padding: 0;
			margin: 0; }
		&--italic {
			display: block;
			min-height: 80px;
			height: auto;
			padding: 10px 20px;
			i {
				@include fontRR;
				font-style: italic; } }
		&--big {
			height: 90px; }
		&--margin-left {
			margin-left: 40px; } }

	&__questions {
		position: absolute;
		bottom: 450px;
		left: 0;
		width: 195px;
		@include fontRB;
		text-align: center;
		font-size: 14px; }

	&__text {
		margin-bottom: 10px;
		&--bold {
			@include fontRB; } }

	&__el-info {
		display: flex;
		flex-grow: 1;
		.form-member__col {
			width: 50%; } }

	&__marrige {
		position: absolute;
		right: -430px;
		top: 10px;
		width: 350px;
		&-name {
			overflow: hidden;
			text-overflow: ellipsis;
			height: 30px;
			line-height: 30px;
			padding: 0 10px;
			width: 100%;
			border: 1px solid $gray;
			margin-top: 15px;
			background-color: $lightGray; } }

