	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 0 0 25px;
		@include fontRR;
		font-size: 16px;
		font-style: italic;
		color: $blue;
		border-left: 1px solid $blue;
		p, ul {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		li {
			position: relative;
			padding-left: 20px;
			&:after {
				content: '';
				position: absolute;
				left: 0;
				top: 10px;
				width: 6px;
				height: 6px;
				background-color: $blue;
				border-radius: 50%; } }

		&--top-bot {
			padding: 20px 0;
			margin-bottom: 20px;
			border-left: none;
			border-top: 1px solid $blue;
			border-bottom: 1px solid $blue;
			p {
				margin-bottom: 0; } } }

	&__input {
		padding: 0 30px;
		&--small {
			width: 250px;
			margin: 0 auto 20px; }
		&--medium {
			width: 385px;
			margin-left: auto; }
		&--center {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			.input {
				width: 150px;
				margin-bottom: 100px; } }
		&--flex {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			.input {
				margin-right: 10px; } }

		&--flex-center {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			.input {
				width: 300px;
				margin-left: 250px;
				margin-right: 10px; } } }

	&__select {
		// padding: 0 30px
		margin-bottom: 20px;
		margin-right: 50px;
		.select {
			height: 30px;
			font-size: 16px;
			line-height: 30px;
			margin-right: 10px;
			border-color: $blue;
			background-color: $lightGray;
			&[disabled] {
				border-color: #979797;
				opacity: .5; } } }


	&__livelihood {
		display: flex;
		flex-direction: column;
		height: 100%;

		&-wrap {
			display: flex;
			flex-grow: 1; }

		&-title {
			margin-bottom: 20px;
			@include fontRM;
			font-size: 18px;
			color: $blue; }

		&-left {
			display: flex;
			flex-direction: column;
			margin-right: 30px; }

		&-right {
			display: flex;
			flex-direction: column; }

		&-radio {
			position: relative;
			margin-bottom: 15px; }


		&-check {
			height: 20px;
			margin-bottom: 15px; } }

	&__job-region {
		position: relative;
		margin-top: 60px;
		padding-left: 110px;
		&.is-disabled {
			opacity: 0.5; }

		.form-member__text {
			font-size: 18px; }
		.form-member__subtitle {
			width: 230px;
			height: auto;
			text-transform: uppercase; }
		.form-member__select {
			flex-grow: 1;
			margin-bottom: 0;
			padding: 0; }
		.form-member__flex {
			margin-bottom: 20px; }
		.form-member__el {
			width: 50%;
			&:last-child {
				margin-bottom: 0; } } }

	&__migration {
		position: relative;
		.form-member__subtitle {
			width: 230px;
			height: auto;
			text-transform: uppercase; }
		.form-member__select {
			flex-grow: 1;
			margin-bottom: 0;
			padding: 0; }
		.form-member__flex {
			margin-bottom: 20px; } }
	#s_member-12 {
		#{$this}__select {
			max-width: 495px; } }

	#s_member-15 {
		.form-member__select {
			margin-right: 0;
			&.form-member__input--flex {
				margin-right: 50px; } } } }

body.android {
	.form-member {
		&__title {
			margin-bottom: 20px; }
		&__subtitle {
			margin-bottom: 15px; }
		&__questions {
			display: none; }
		&__col {
			margin-bottom: 15px;
			&--absolute {
				bottom: -50px;
				&-a8 {
					bottom: -310px; } } }

		&__icon-tooltip {
			&--a1 {
				top: 87px; } }

		&__flex {
			&--column {
				max-height: 100%;
				justify-content: flex-start;
				height: auto; } }

		#s_member-1 {
			.radio {
				max-width: 440px; } }

		&__inner {
			padding: 20px 30px;
			min-height: calc(100vh - 120px); }

		&__livelihood-check, &__livelihood-radio {
			margin-bottom: 12px; }

		&__livelihood-title {
			margin-bottom: 15px; } } }
