.popup-cert {
	$this: &;
	display: none;
	@include vcenter;
	position: fixed;
	width: 100vw;
	height: 100vh;
	background-color: rgba($black,.5);
	z-index: 20;
	&__container {
		@include vcenter;
		width: 800px;
		padding: 30px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; }
	&__title {
		@include fontRM;
		font-size: 24px;
		text-transform: uppercase;
		text-align: center;
		margin: 0 auto; }
	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		z-index: 10;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }
	&__anchor {
		position: relative; }
	&.is-show {
		display: block; }
	&__content {
		padding: 20px;
		&-block {
			padding: 0 0 20px 0; } }
	&__btns {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between; }
	&__btn {
		width: auto; }
	&.warning {
		#{$this}__title span {
			padding-left: 50px;
			color: $orange;
			@include image('../img/warning.svg');
			background-repeat: no-repeat;
			background-position: left center; } }
	p {
		text-indent: 20px;
		padding-bottom: 5px; }
	p:last-child {
		font-style: italic; }

	&__files-label {
		max-width: calc(100% - 150px); }
	&__files-caption {
		width: 150px; } }
