.popup-control-all {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	height: 100vh;
	padding: 100px 0;
	z-index: 30;
	line-height: 1.4;
	overflow-y: scroll;
	background-color: rgba($black,0.5);
	&.is-show {
		display: block; }
	&.is-error button[data="continue"] {
		display: none; }


	&__container {
		position: relative;
		width: 80vw;
		margin: 0 auto;
		padding: 50px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; }

	&__btns {
		display: flex;
		justify-content: space-between; }

	&__btn {
		width: 48%; }


	&__title {
		position: relative;
		display: block;
		width: 360px;
		margin: 0 auto 20px;
		padding-left: 40px;
		@include fontRB;
		font-size: 24px;
		text-transform: uppercase;
		color: $blue;
		text-align: center;
		@include image('../img/info.svg');
		background-repeat: no-repeat;
		background-size: 30px; }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		z-index: 10;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }

	&__content {
		display: flex; }

	&__info {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 40%;
		padding-right: 20px;
		margin-bottom: 20px;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			width: 1px;
			height: 100%;
			background-color: $blue; } }



	&__row {
		margin-bottom: 20px;
		padding-left: 100px;
		background-repeat: no-repeat;
		background-position: left center;
		background-size: 70px;
		&:first-child {
			padding-left: 0; }
		b {
			@include fontRB; }

		&--ok {
			@include image('../img/member-ok.svg'); }
		&--error {
			@include image('../img/error.svg'); }
		&--warning {
			@include image('../img/warning.svg'); } }

	&__errors {
		margin-bottom: 30px;
		h3 {
			@include fontRB;
			font-size: 16px;
			line-height: 1.6;
			margin-bottom: 5px; }
		b {
			@include fontRB; }
		p {
			position: relative;
			padding-left: 40px;
			margin-bottom: 5px;
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				left: 30px;
				width: 5px;
				height: 2px;
				background-color: $black; } }

		ol {
			margin-bottom: 15px;
			padding-left: 50px;
			// list-style-type: decimal
			li {
				margin-bottom: 5px; }
			a {
				color: $black;
				&:hover {
					color: $blue;
					text-decoration: underline; } } }

		.error {
			color: $red; }
		.warning {
			color: $yellow; }

		.error-icon {
			padding-left: 25px;
			@include image('../img/error.svg');
			background-repeat: no-repeat;
			background-size: 18px;
			background-position: left center; }
		.warning-icon {
			padding-left: 25px;
			@include image('../img/warning.svg');
			background-repeat: no-repeat;
			background-size: 18px;
			background-position: left center; } } }
