.indiv-login {
	display: none;
	position: relative;
	// top: 40px
	// left: 0
	width: 100%;
	min-height: 600px;
	height: calc(100vh - 40px);
	background-color: $white;
	z-index: 50;
	&.is-show {
		display: block; }

	&__content {
		display: none;
		@include vcenter;
		top: 40%;
		flex-direction: column;
		width: 850px;
		padding: 50px;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
		border-radius: 0 0 5px 5px;
		.section__subtitle {
			width: 300px; }
		&.is-show {
			display: flex; } }
	& button[type="submit"].is-disabled {
		background-color: $gray;
		pointer-events: none; } }
