.choices__list--dropdown {
  position: absolute;
  top: 40px;
  left: 0;
}

.choices__list--dropdown input {
  height: 40px;
}

.choices__list--dropdown .choices__placeholder {
  display: none;
}

.choices__list--dropdown .choices__list {
  max-height: 300px;
}

.choices__list--multiple {
  display: flex;
}

