}

.choices[data-type*=select-one]:before, .choices[data-type*=select-multiple]:before {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  background-color: #0d3f88;
  border: none;
  margin: 0;
  padding: 0;
}

.choices[data-type*=select-one]:after, .choices[data-type*=select-multiple]:after {
  content: '';
  position: absolute;
  right: 12px;
  top: 11px;
  width: 14px;
  height: 14px;
  background-color: transparent;
  border: none;
  border-right: 4px solid #ffffff;
  border-bottom: 4px solid #ffffff;
  transform: rotate(45deg);
}

