  opacity: 0.5;
}

.choices.is-disabled .choices__inner {
  background-color: #F5F8FD;
  border-color: #979797;
}

.choices.is-disabled[data-type*=select-one]:before, .choices.is-disabled[data-type*=select-multiple]:before {
  cursor: no-drop;
}

