.finish {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $white;
	z-index: 100;

	&__content {
		@include vcenter;
		top: 40%; }

	&__code {
		padding: 20px;
		@include fontRM;
		font-size: 32px;
		border: 5px solid $blue;
		border-radius: 5px;
		margin-bottom: 40px;
		text-align: center; }

	&__text {
		@include fontRB;
		font-size: 18px;
		text-align: center;
		margin-bottom: 10px;
		&--small {
			@include fontRR;
			font-size: 14px; }
		span {
			@include fontRR; } }

	&__done {
		position: absolute;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
		width: 240px; } }
