.navigation-wizard {
	display: flex;
	justify-content: space-between;
	position: absolute;
	right: 20px;
	bottom: 20px;
	width: 905px;

	&__item {
		width: 220px;

		&--loader.waiting {
			.loader {
				display: block; }
			.btn-blue-icon__icon, .btn-blue-icon__text {
				display: none; } }
		&--large {
			width: 400px; } }

	&__help {
		background-color: $transp;
		width: 50px;
		height: 50px;
		&.is-active {
			animation: motion 0.25s infinite; }
		&:hover {
			animation: none;
			transform: scale(1.05);
			svg {
				border-radius: 50%;
				fill: $blueHover;
				box-shadow: 0 2px 34px 1px rgba($black, 0.4); } } } }

@keyframes motion {
	0% {
		transform: rotate(0deg) scale(1); }

	25% {
		transform: rotate(10deg) scale(1.05); }

	50% {
		transform: scale(1.1); }

	75% {
		transform: rotate(-10deg) scale(1.05); }

	100% {
		transform: rotate(0deg) scale(1); } }

.root[data-section="auth"], .root[data-section="indiv"] {
	.navigation-wizard {
		display: none;
		&.is-show {
			display: flex; } } }

body.android {
	.navigation-wizard {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 15px 50px;
		background-color: $blue;
		z-index: 10;

		&__item {
			background-color: $white;
			color: $blue;
			svg {
				fill: $blue; }
			&.disabled {
				background-color: $gray;
				color: $white;
				svg {
					fill: $white; } } }

		&__help svg {
			fill: $white; } } }
