.radio-num {
	$this: &;
	position: relative;
	display: flex;
	// transition: all 0.2s ease
	z-index: 1;
	&:hover {
		#{$this}__label:before {
			box-shadow: 0 0 34px 1px rgba($black,.4);
			background-color: $blueHover; }
		#{$this}__input:checked + #{$this}__label:before {
			background-color: $white;
			box-shadow: 0 0 34px 1px rgba($black,.4); } }

	&__input {
		display: none; }

	&__label {
		width: 60px;
		height: 60px;
		@include fontRB;
		font-size: 28px;
		line-height: 60px;
		text-align: center;
		color: $white;
		cursor: pointer;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			height: 60px;
			width: 60px;
			background-color: $blue;
			border-radius: 50%;
			z-index: -1;
 } }			// transition: all 0.2s ease

	&__input:checked + &__label {
		width: 60px;
		height: 60px;
		font-size: 32px;
		line-height: 60px;
		color: $black;
		&:before {
			width: 60px;
			height: 60px;
			background-color: $white;
			border: 2px solid $blue;
			box-shadow: 0px 4px 4px rgba($black, 0.25); } }

	&--small {
		#{$this}__label {
			width: 67px;
			height: 67px;
			font-size: 36px;
			line-height: 67px;
			&:before {
				width: 67px;
				height: 67px; } }
		#{$this}__input:checked + #{$this}__label {
			width: 84px;
			height: 84px;
			font-size: 48px;
			line-height: 84px;
			color: $black;
			&:before {
				width: 84px;
				height: 84px; } } }

	&--big {
		#{$this}__label {
			width: 80px;
			height: 80px;
			font-size: 48px;
			line-height: 80px;
			&:before {
				width: 80px;
				height: 80px; } }
		#{$this}__input:checked + #{$this}__label {
			width: 100px;
			height: 100px;
			font-size: 72px;
			line-height: 100px;
			color: $black;
			&:before {
				width: 100px;
				height: 100px; } }
		&:hover {
			transform: scale(1);
			#{$this}__label:before {
				box-shadow: 0 0 34px 1px rgba($black,.4);
				background-color: $blueHover; }
			#{$this}__input:checked + #{$this}__label:before {
				background-color: $white; } } } }
