.family {

	&__content {
		position: relative;
		width: 100%;
		height: 100%; }

	&__tree {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%;
		overflow: scroll;
		max-height: 715px;
		&-row {
			display: flex;
			height: 174px;
			flex-shrink: 0;
			&[data="5"] {
				margin-bottom: 160px; }

 } }			// justify-content: center

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		@include fontRR;
		font-size: 16px;
		font-style: italic;
		color: $blue;
		padding: 20px 0;
		margin: 20px;
		border-top: 1px solid $blue;
		border-bottom: 1px solid $blue;
		p {
			margin-bottom: 10px; } }

	&__btn-add {
		@include vcenter;
		width: 200px;
		height: 200px;
		padding-top: 145px;
		@include fontRB;
		font-size: 18px;
		color: $black;
		text-transform: uppercase;
		background: $white;
		border: 2px solid $blue;
		box-shadow: 0px 4px 4px rgba($black, 0.25);
		border-radius: 5px;
		transition: transform 0.2s ease;
		$btnAdd: &;
		&-icon {
			@include vcenter;
			top: 85px;
			width: 100px;
			height: 100px;
			&:before {
				content: '';
				position: absolute;
				top: 40px;
				left: 50%;
				transform: translateX(-50%);
				width: 100px;
				height: 20px;
				background-color: $green; }
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				width: 20px;
				height: 100px;
				background-color: $green; } }
		&.is-small {
			width: 100px;
			height: 100px;
			font-size: 14px;
			padding-top: 60px;
			#{$btnAdd}-icon {
				top: 60px;
				&:before {
					top: 21px;
					width: 52px;
					height: 10px; }
				&:after {
					width: 10px;
					height: 52px; } } }
		&:hover {
			transform: scale(1.1) translate(-45%, -45%);
			box-shadow: 0px 4px 34px rgba($black, 0.25); } } }

