.btn-blue-icon {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	padding: 10px 30px;
	@include fontRB;
	font-size: 18px;
	color: $white;
	text-transform: uppercase;
	background-color: $blue;
	border-radius: 5px;
	box-shadow: 0 2px 34px 1px rgba($black, 0.4);
	&__icon {
		display: flex;
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		&--left {
			right: auto;
			left: 10px;
			transform: translateY(-50%) rotate(180deg); } }
	&:hover {
		background-color: $blueHover;
		box-shadow: 0 2px 34px 5px rgba($black, 0.4);
		.btn-blue-icon__icon {
			animation: arrow 0.8s linear infinite; }
		.btn-blue-icon__icon--left {
			animation: arrow-left 0.8s linear infinite; } }

	&.disabled {
		pointer-events: none;
		background-color: $gray; }
	&:active {
		transform: translateY(2px); } }

@keyframes arrow {
	0% {
		transform: translate(0, -50%); }
	50% {
		transform: translate(6px, -50%); }
	100% {
		transform: translateX(0, -50%); } }

@keyframes arrow-left {
	0% {
		transform: translate(0, -50%) rotate(180deg); }
	50% {
		transform: translate(-6px, -50%) rotate(180deg); }
	100% {
		transform: translateX(0, -50%) rotate(180deg); } }

.btn-icon {
	display: flex;
	align-items: center;
	width: 100%;
	padding: 0 10px;
	@include fontRB;
	font-size: 14px;
	text-transform: uppercase;
	color: $blue;
	background-color: $white;
	border: 1px solid $blue;
	border-radius: 5px;
	box-shadow: 0 2px 34px 1px rgba($black, 0.4);
	transition: transform 0.1s ease;
	&__icon {
		height: 30px;
		width: 30px;
		margin-right: 15px;
		img, svg {
			width: 100%;
			height: 100%; } }

	&:hover {
		transform: scale(1.05); }

	&:active {
		transform: translateY(2px); } }

.btn {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	padding: 10px 30px;
	@include fontRB;
	font-size: 18px;
	color: $white;
	text-transform: uppercase;
	background-color: $blue;
	border-radius: 5px;
	box-shadow: 0 2px 34px 1px rgba($black, 0.4);
	&:hover {
		background-color: $blueHover; }

	&:active {
		transform: translateY(2px); } }
