.indiv-houses {

	&__table-row {
		display: grid;
		grid-template-columns: 10% 25% 35% 15% 15%;
		border-bottom: 1px solid $blue;
		color: $black;
		&-header {
			background-color: $blue;
			color: $white;
			@include fontRM;
			text-transform: uppercase; }
		&--disabled {
			pointer-events: none; } }

	&__table-col {
		padding: 15px 0;
		text-align: center;
		&:first-child {
			text-align: left;
			padding-left: 30px; }
		&:last-child {
			padding-right: 30px; } } }
