.indiv-header-houses {
	display: flex;
	justify-content: space-between;
	margin: 15px 0;
	padding: 0 30px;

	&__title {
		margin-bottom: 30px;
		@include fontRB;
		font-size: 26px;
		text-transform: uppercase; }
	&__text {
		margin-right: 20px;
		@include fontRM;
		color: $black; }

	&__btn-nav {
		justify-content: flex-start;
		font-size: 14px;
		margin-bottom: 10px;
		padding: 10px 40px;
		border: 1px solid $blue;
		&:last-child {
			margin-bottom: 0; }
		&.is-active {
			background-color: $white;
			border: 1px solid $blue;
			color: $blue; }
		&.is-hide {
			display: none; } }
	&__btn-nav-text {
		width: 100%;
		display: flex;
		justify-content: space-between;
		& span:first-child {
			margin-right: 10px; } }

	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		&:nth-child(2) {
			display: none; } }

	& &__select {
		min-width: 200px;
		.selectize-input {
			display: flex;
			& > input {
				min-width: 100%!important;
				font-size: 14px;
				@include placeholder {
					font-size: 14px; } }
			&.input-active {
				display: flex; } }
		.selectize-input {
			height: 30px;
			line-height: 30px;
			&:before {
				width: 30px;
				height: 30px; }
			&:after {
				right: 8px;
				top: 5px; }
			&.dropdown-active:after {
				top: 10px; } }

		.selectize-dropdown {
			top: 30px!important; } } }
