$lineWidth: 174px;

.member {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex-shrink: 0;
	width: 124px;
	height: 124px;
	margin: 25px;
	@include fontRR;
	font-size: 12px;
	border-radius: 15px;
	background-color: $white;
	cursor: pointer;
	transition: box-shadow 0.4s ease;
	$this: &;

	&:hover {
		.icon-woman {
			transition: fill 0.4s;
			fill: $pink;
			opacity: 0.7; }
		.icon-man {
			transition: fill 0.4s;
			fill: $blue;
			opacity: 0.7; }

		.popup-member {
			display: flex;
			&.down.pair-right .line-pair {
				display: none; } } }
	&--man:hover {
		box-shadow: 0px 0px 25px 5px rgba($blue, 0.4); }
	&--woman:hover {
		box-shadow: 0px 0px 25px 5px rgba($pink, 0.4); }


	&--man:after {
		content: '';
		position: absolute;
		top: -3px;
		left: -3px;
		width: 124px;
		height: 124px;
		border-radius: 15px;
		z-index: 1;
		border: 3px solid $blue; }

	&--woman:after {
		content: '';
		position: absolute;
		top: -3px;
		left: -3px;
		width: 124px;
		height: 124px;
		border-radius: 15px;
		z-index: 1;
		border: 3px solid $pink; }

	&[data-first="true"] {
		#{$this}__progress {
			right: -25px; }
		&:after {
			content: '';
			position: absolute;
			top: -7px;
			left: -7px;
			width: 132px;
			height: 132px;
			border-radius: 20px;
			z-index: 1; } }

	&--man[data-first="true"]:after {
		border: 7px solid $blue; }

	&--woman[data-first="true"]:after {
		border: 7px solid $pink; }

	&--man {
		border: 3px solid $blue;
		box-shadow: 0px 4px 4px rgba($black, 0.25);
		#{$this}__close {
			background-color: $blue; } }
	&--woman {
		border: 3px solid $pink;
		box-shadow: 0px 4px 4px rgba($black, 0.25);
		#{$this}__close {
			background-color: $pink; } }

	&__close {
		position: absolute;
		top: -3px;
		right: -3px;
		width: 25px;
		height: 25px;
		border-radius: 50%;
		z-index: 6;
		transition: all 0.2s ease;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 11px;
			transform: rotate(45deg);
			width: 3px;
			height: 16px;
			background-color: $white; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			transform: scale(1.4);
			& + .popup-member {
				display: none; } } }

	&__text {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		max-width: 85px;
		padding-left: 6px;
		padding-top: 6px;
		@include fontRB;
		font-size: 12px;
		text-align: left;
		line-height: 1.4;
		z-index: 5;
		overflow: hidden;
		span {
			position: absolute;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(to right, $transp 50% , $white); }
		&--big {
			font-size: 18px;
			padding-top: 0;
			background-color: $white;
			border-radius: 0 0 0 12px; } }

	&__connection {
		position: relative;
		padding-left: 6px;
		@include fontRR;
		font-style: italic;
		font-size: 11px;
		text-align: left;
		z-index: 2; }

	&__icon {
		display: flex;
		@include vcenter;
		font-size: 70px;
		.icon-man {
			fill: $gray2; }
		.icon-woman {
			fill: $gray2; }
		&-gender {
			display: flex;
			position: absolute;
			bottom: 0;
			right: 0;
			font-size: 30px;
			background-color: $white;
			z-index: 1;
			border-radius: 0 0 12px 0; } }

	&__progress {
		position: absolute;
		top: 0;
		right: -20px;
		display: flex;
		align-items: flex-end;
		width: 10px;
		height: 100%;
		border: 1px solid $gray;
		border-radius: 5px;
		pointer-events: none;
		z-index: 1;
		span {
			width: 100%;
			background-color: $green;
			border-radius: 0 0 5px 5px; } }

	&__valid {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black, 0.5);
		border-radius: 12px;
		z-index: 5;
		&-icon {
			width: 60px;
			height: 60px;
			@include vcenter;
			background-repeat: no-repeat;
			background-size: 100%; } }

	&.warning {
		#{$this}__valid {
			display: block;
			&-icon {
				@include image('../img/member-warning.svg'); } } }

	&.error {
		#{$this}__valid {
			display: block;
			&-icon {
				@include image('../img/member-error.svg'); } } }

	&.ok {
		#{$this}__valid {
			display: block;
			&-icon {
				@include image('../img/member-ok.svg'); } } }

	&.fake {
		border-color: transparent;
		cursor: initial; }
	.line {
		display: none;
		position: absolute;
		width: $lineWidth;
		height: 10px;
		background-color: $blue;
		pointer-events: none; }

	.line-pair {
		display: none;
		position: absolute;
		pointer-events: none;
		width: 84px;
		height: 56px;
		font-size: 50px; }

	&.up-long:before {
			content: '';
			position: absolute;
			top: -53px;
			left: 50%;
			transform: translateX(-50%);
			width: 10px;
			height: 50px;
			background-color: $blue; }

	&.down {
		&.pair-right .line-pair {
			display: block;
			right: -70px;
			bottom: -30px; }

		&.pair-left .line-pair {
			display: block;
			left: -70px;
			bottom: -30px; } }
	&.up {
