.header {
	padding: 5px 0;
	margin-bottom: 30px;
	color: $white;
	background-color: $blue;

	&__in {
		display: flex;
		// justify-content: space-between
		align-items: center; }

	&__logo {
		position: relative;
		display: flex;
		align-items: center;
		@include fontRM;
		font-size: 14px;
		color: $white;
		img {
			width: 30px;
			height: 30px;
			margin-right: 20px; }
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 40px;
			width: 3px;
			height: calc( 50% - 5px);
			background-color: #0187cd; }

		&:after {
			top: auto;
			bottom: 5px;
			background-color: #ffd500; } }

	&__btn {
		height: 40px;
		width: 200px; }

	&__title {
		display: flex;
		margin-left: auto;
		@include fontRB;
		font-size: 20px;
		span {
			margin-right: 10px; } }

	&__burger {
		display: block;
		height: auto;
		width: 28px;
		margin-right: 40px;

		background-color: transparent;

		span {
			transition: 0.3s;
			display: block;
			height: 3px;
			width: 100%;

			background-color: $white;
			border-radius: 10px;

			&:not(:last-child) {
				margin-bottom: 4px; } }

		&.is-active {
			span {
				&:nth-child(1) {
					transform: translateY(7px) rotate(45deg); }

				&:nth-child(2) {
					transform: translateX(-100%);
					opacity: 0;
					visibility: hidden; }

				&:nth-child(3) {
					transform: translateY(-7px) rotate(-45deg); } } } } }

.root[data-section="auth"] {
	.header__btn {
		display: none; } }

body.android {
	.header {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		padding: 5px 30px;
		margin-bottom: 0;
		z-index: 10;
		&__btn {
			display: none; }

		.container {
			padding: 0;
			height: auto; } } }



