.radio {
	$this: &;
	position: relative;
	padding-left: 30px;
	&.is-disabled {
		pointer-events: none;
		opacity: 0.5; }
	&__input {
		opacity: 0;
		position: absolute;
		bottom: 20px;
		right: 0;
		z-index: -1; }

	&__label {
		cursor: pointer;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 4px;
			height: 16px;
			width: 16px;
			border: 2px solid $gray;
			border-radius: 50%; }

		&:after {
			display: none;
			content: '';
			position: absolute;
			left: 4px;
			top: 8px;
			height: 8px;
			width: 8px;
			background-color: $blue;
			border-radius: 50%; } }

	&__subtitle {
		display: flex;

		span {
			@include fontRB;
			font-size: 14px;
			margin-right: 20px; } }


	&.disabled {
		color: $gray; }


	&__input:checked + &__label:after {
		display: block; }

	&__input:checked + &__label:before {
		border-color: $blue; }

	&--top {
		@include fontRM;
		padding-left: 0;
		padding-top: 25px;
		#{$this}__label:before, #{$this}__label:after {
			left: 50%;
			transform: translateX(-50%); } } }
