.popup-get-coordinate {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 15;
	&.is-show {
		display: block; }

	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background-color: rgba($black, 0.5);
		z-index: -1; }

	&__content {
		@include vcenter;
		width: 850px;
		padding: 30px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px;
		z-index: 10; }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		z-index: 10;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }

	&__title {
		@include fontRB;
		font-size: 24px;
		color: $blue;
		text-align: center;
		margin-bottom: 80px; }

	&__btns {
		display: flex;
		justify-content: space-between;
		margin-top: 20px; }

	&__btn {
		max-width: 350px;
		margin-right: 20px;
		&:last-child {
			margin-right: 0; } } }
