.popup-check {
	$this: &;
	display: none;
	@include vcenter;
	position: fixed;
	width: 100vw;
	height: 100vh;
	padding: 100px 0;
	background-color: rgba($black,.5);
	z-index: 20;
	overflow-y: scroll;
	&__container {
		// +vcenter
		position: relative;
		width: 800px;
		padding: 30px;
		margin: auto;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; }
	&__title {
		@include fontRM;
		font-size: 24px;
		text-transform: uppercase;
		text-align: center;
		margin: 0 auto; }
	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		z-index: 10;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }
	&__anchor {
		position: relative; }
	&.is-show {
		display: flex; }
	&__content {
		padding: 20px; }
	&__btns {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between; }
	&__btn {
		display: none;
		width: auto;
		&.is-show {
			display: block; } }
	&__subtitle {
		@include fontRB; }
	ol {
		padding-left: 40px;
		margin: 15px 0;
		list-style-type: decimal;
		li {
			margin-bottom: 5px;
			a {
				color: $black;
				&:hover {
					color: $blue;
					text-decoration: underline; } } } }

	&.warning {
		#{$this}__title span {
			padding-left: 50px;
			color: $orange;
			@include image('../img/warning.svg');
			background-repeat: no-repeat;
			background-position: left center; } }

	&.error {
		#{$this}__title span {
			padding-left: 50px;
			color: $red;
			@include image('../img/error.svg');
			background-repeat: no-repeat;
			background-position: left center; } } }

