.modal {
	// display: none
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	padding: 100px 0;
	overflow-y: scroll;
	z-index: 10;
	background-color: rgba($black, 0.5);
	&.is-show {
		display: block; }

	&__content {
		width: 800px;
		margin: 0 auto;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; } }
