.indiv-control-tasks {

	&__table-row {
		display: grid;
		grid-template-columns: 10% 20% 25% 15% 15% 15%;
		border-bottom: 1px solid $blue;
		color: $black;
		&-header {
			background-color: $blue;
			color: $white;
			@include fontRM;
			text-transform: uppercase; }

		&--disabled {
			pointer-events: none; } }

	&__table-col {
		padding: 15px 0;
		text-align: center;
		&:first-child {
			text-align: left;
			padding-left: 30px; } }

	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 15px 0;
		padding: 0 30px;

		&-item:first-child {
			width: 380px;
			font-size: 16px; }

		&-title {
			@include fontRB;
			font-size: 28px;
			color: $blue;
			text-align: right; }

		&-text {
			text-align: right; } } }
