.tabs {
	position: relative;
	display: flex;
	width: 945px;
	height: 850px;
	margin-left: auto;
	background-color: $white;
	box-shadow: 0px 4px 12px rgba($black, 0.25);
	border-radius: 5px;
	line-height: 1.3;
	.tabs {
		flex-direction: column; }

	&__nav-title {
		position: absolute;
		left: -195px;
		bottom: 0;
		display: flex;
		flex-wrap: wrap;
		width: 195px;
		margin: 0;
		list-style-type: none;
		z-index: 1;
		li {
			position: relative;
			display: flex;
			width: 58px;
			height: 58px;
			margin-right: 5px;
			margin-bottom: 5px;
			@include fontRM;
			font-size: 16px;
			text-align: center;
			background-color: $blue;
			border-radius: 5px;
			transition: all 0.2s ease;
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: 100%;
				color: $white;
				border-radius: 5px;
				z-index: 1; }
			&:hover {
				transform: scale(1.1);
				background-color: $blueHover; }

			&.is-active {
				background-color: $white;
				a {
					color: $black;
					box-shadow: 0 2px 34px 1px rgba(0,0,0,.4); } }

			&.circle {
				&:before {
					content: '';
					position: absolute;
					top: 2px;
					left: 2px;
					width: 16px;
					height: 16px;
					background-color: $transp;
					border-radius: 50%; } }

			&.warning.circle {
				&:before {
					background-color: $yellow; } }
			&.error.circle {
				&:before {
					background-color: $red; } }
			&.ok.circle {
				&:before {
					background-color: $brightGreen; } }
			&.disabled {
				background-color: $gray;
				a {
					color: $white;
					box-shadow: none;
					cursor: no-drop; }
				&:hover {
					transform: scale(1); } } }

		&--top {
			left: 0;
			top: 0;
			bottom: auto;
			width: 100%;
			li {
				margin: 0;
				width: 50%;
				height: 50px;
				border-radius: 0;
				&.circle:before {
					@include vcenter;
					left: 20px; }
				&:first-child {
					border-radius: 5px 0 0 0; }
				&:last-child {
					border-radius: 0 5px 0 0; }
				a {
					border-radius: 0; }
				&:hover {
					transform: scale(1); }
				&.is-active {
					a {
						box-shadow: none; } } } }

		&--left {
			position: absolute;
			left: -195px;
			bottom: 0;
			width: 195px;
			flex-direction: column;
			li {
				width: 100%;
				height: 60px;
				@include fontRR;
				font-size: 16px;
				line-height: 60px;
				border-right: none;
				border-bottom: 1px solid $white;

				a {
					text-align: left;
					padding: 0 0 0 25px; }

				&:hover {
					transform: scale(1); }
				&.is-active a {
					box-shadow: -4px 4px 4px rgba($black, 0.25);
					z-index: 1;
					border-radius: 5px;
					&:first-child {
						border-radius: 5px; }
					&:last-child {
						border-radius: 5px; } } } }

		&--other {
			bottom: auto;
			top: 500px; }

		&--center li a {
			text-align: center;
			padding: 0; }

		&--members {
			display: block;
			left: -205px;
			width: 205px;
			height: 440px;
			padding-left: 10px;
			padding-bottom: 10px;
			li {
				border-radius: 5px 0 0 5px;
				&.circle:before {
					top: 4px;
					left: 4px; }
				&.is-active a {
					box-shadow: -5px 0 4px 1px rgba($black, 0.25); }
				a {
					&:first-child {
						border-radius: 5px 0 0 0; } } }
			&.is-scroll {
				overflow-y: scroll; } } }


	&__contents {
		display: flex;
		flex-direction: column;
		width: 100%;
		&--fix-height {
			height: 710px; }
		&--m-top {
			margin-top: 50px; } }
	&__content {
		height: 100%;
		height: 100%;
		display: none;
		&.is-active {
			display: block;
			width: 100%; } } }

body.android {
	.tabs {
		height: auto;
		width: calc(100% - 270px);
		margin-bottom: 80px;
		box-shadow: none;
		border-radius: 0;
		& .tabs {
			width: 100%;
			margin-bottom: 0; }

		&__nav-title {
			position: fixed;
			top: 40px;
			bottom: 80px;
			left: 0;
			width: 270px;
			background-color: $blue;
			border-top: 1px solid $white;
			border-bottom: 1px solid $white;
			box-shadow: 0 0px 4px 1px rgba(0,0,0,.4);

			li {
				width: 90px;
				height: calc(100% / 5);
				margin: 0;
				border-right: 1px solid $white;
				border-bottom: 1px solid $white;
				border-radius: 0;
				&:nth-child(3n) {
					border-right: none; }
				&:nth-last-child(-n+3) {
					border-bottom: none; }
				&[data-info="m_18"] {
					border-bottom: 1px solid $white; }
				a {
					border-radius: 0; }
				&.is-active {
					a {
						box-shadow: none; } }
				&:hover {
					transform: scale(1); } }

			&--top {
				// position: absolute
				left: 272px;
				top: 40px;
				bottom: auto;
				width: calc(100% - 272px);
				box-shadow: 4px 0px 4px 1px rgba(0,0,0,.4);
				border-bottom: none;
				z-index: 10;
				li {
					margin: 0;
					width: 50%;
					height: 50px;
					border: none;
					border-radius: 0;
					&.circle:before {
						@include vcenter;
						left: 20px; }
					a {
						border-radius: 0; }
					&:hover {
						transform: scale(1); }
					&.is-active {
						a {
							box-shadow: none; } } } }
			&--members {
				display: block;
				width: 270px;
				height: calc(100% - 120px);
				padding-left: 0;
				padding-bottom: 0;
				background-color: $white;
				li {
					width: 100%;
					height: 80px;
					border-right: none;
					box-shadow: none;
					border-radius: 0;
					border-bottom: 1px solid $white;
					&.circle:before {
						top: 4px;
						left: 4px; }
					&.is-active a {
						box-shadow: 0 0px 4px 1px rgba(0,0,0,.4); }
					a {
						&:first-child {
							border-radius: 0; } }
					&:nth-last-child(-n+2) {
						border-bottom: none; }
					&:nth-last-child(-n+3) {
						border-bottom: 1px solid $white; } }
				&.is-scroll {
					overflow-y: scroll; } } } }

	.s_member {
		.tabs {
			&__nav-title li {
				height: calc(100% / 7); } } } }
