.popup-get-pass {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 100;
	&.is-show {
		display: block; }

	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background-color: rgba($black, 0.5);
		z-index: -1; }

	&__content {
		@include vcenter;
		width: 60%;
		padding: 50px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px;
		z-index: 10; }

	&__btns {
		display: flex;
		justify-content: space-between; }

	&__btn {
		width: 240px; }


	&__title {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 50px;
		background-color: $white;
		@include fontRB;
		font-size: 24px;
		color: $blue;
		text-align: center; } }
