.popup-member {
	display: none;
	justify-content: space-between;
	flex-direction: column;
	position: absolute;
	bottom: -180px;
	left: -20px;
	width: 350px;
	height: 180px;
	padding: 20px;
	z-index: 10;
	border-radius: 5px;
	box-shadow: 0 4px 4px rgba($black, 0.25);
	background-color: $gray2;
	border: 2px solid $blue;
	&.is-show {
		display: flex; }

	&:before {
		content: '';
		position: absolute;
		top: -15px;
		left: 70px;
		transform: rotate(45deg);
		width: 30px;
		height: 30px;
		border: 2px solid $blue;
		background-color: $gray2;
		border-bottom-color: $transp;
		border-right-color: $transp; }


	&__text {
		@include fontRR;
		font-size: 18px;
		max-width: 210px;
		text-align: left; }

	&__btns {
		display: flex; }
	&__btn {
		@include fontRM;
		width: 150px;
		margin-right: 20px;
		padding: 6px 15px;
		font-size: 16px;
		text-align: center;
		color: $white;
		background-color: $blue;
		border-radius: 5px;
		&:last-child {
			margin-right: 0; }
		&:hover {
			opacity: 0.8; } } }
