.navigation {
	display: none;
	justify-content: space-between;
	position: absolute;
	right: 20px;
	bottom: 0;
	width: 905px;

	&__item {
		width: 360px;



		&--loader.waiting {
			.loader {
				display: block; }
			.btn-blue-icon__icon, .btn-blue-icon__text {
				display: none; } }
		&--large {
			width: 400px; } }

	&__help {
		background-color: $transp;
		width: 50px;
		height: 50px; } }

.root[data-section="auth"] {
	.navigation {
		position: static;
		display: flex;
		width: 100%;
		&__help {
			display: none; }
		&__item:last-child {
			margin-left: auto; } } }
