.form-room {

	&__inner {
		padding: 30px;
		height: 100%;
		position: relative; }

	&__flex {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		&--column {
			flex-direction: column;
			height: 95%; }
		&--center {
			align-items: center; }
		&--start {
			justify-content: flex-start;
			.radio {
				margin-right: 20px; } } }

	&__col {
		width: 48.5%;
		margin-bottom: 20px;
		&--right {
			margin-left: auto; }
		&--mb-big {
			margin-bottom: 40px; }
		.input {
			margin-top: 15px; }
		&.is-disabled {
			opacity: 0.5;
			pointer-events: none; }
		&--tablet {
			position: absolute;
			bottom: 140px; } }


	&__el {
		position: relative; // для відмови від відповіді static
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0; }
		&--mb-big {
			margin-bottom: 40px; }
		&--mb-medium {
			margin-bottom: 20px; }
		.input {
			margin-top: 15px; }
		&--absolute {
			width: 360px;
			position: absolute;
			&-p4 {
				bottom: 0;
				right: -100%; } }
		&.is-disabled {
			opacity: 0.5;
			pointer-events: none; }
		&--tablet {
			position: absolute;
			left: 30px;
			bottom: 140px; } }

	&__icon-tooltip {
		position: absolute;
		&--p4 {
			width: 20px;
			height: 35px;
			border: 2px solid $blue;
			border-left: none;
			left: -70px;
			top: 45px;
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				right: -30px;
				width: 30px;
				height: 2px;
				background-color: $blue; }
			&:after {
				content: '';
				position: absolute;
				top: 12px;
				right: -30px;
				width: 10px;
				height: 10px;
				border: 2px solid $blue;
				border-left: none;
				border-bottom: none;
				transform: rotate(45deg); } } }


	&__title {
		width: 100%;
		@include fontRB;
		font-size: 24px;
		text-align: center;
		margin-bottom: 30px; }

	&__subtitle {
		display: flex;
		align-items: center;
		height: 50px;
		padding: 0 20px;
		margin-bottom: 20px;
		@include fontRM;
		font-size: 16px;
		background-color: $lightBlue;
		&--transp {
			background-color: $transp;
			padding: 0;
			margin: 0;
			width: 100%; } }

	&__questions {
		position: absolute;
		bottom: 320px;
		left: -200px;
		width: 200px;
		@include fontRB;
		text-align: center;
		font-size: 14px; }

	&__el-info {
		display: flex;
		flex-grow: 1; }

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 0 0 25px;
		@include fontRR;
		font-size: 16px;
		font-style: italic;
		color: $blue;
		border-left: 1px solid $blue;
		p, ul {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		li {
			position: relative;
			padding-left: 20px;
			&:after {
				content: '';
				position: absolute;
				left: 0;
				top: 10px;
				width: 6px;
				height: 6px;
				background-color: $blue;
				border-radius: 50%; } } }

	&__input {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.input {
			width: 150px;
			margin-bottom: 15px; }
		&-title {
			@include fontRB;
			font-size: 18px;
			margin-bottom: 100px; } } }

body.android {
	.form-room {
		&__questions {
			display: none; }

		&__flex {

			&--column {
				height: auto; } }

		&__inner {
			min-height: calc(100vh - 120px);
			padding: 20px 30px; }

		&__subtitle {
			margin-bottom: 15px; }

		&__col {
			margin-bottom: 15px; }

		&__title {
			margin-bottom: 20px; }

		&__input-title {
			margin-bottom: 0; } } }

