.input-file {
	position: relative;
	width: 100%;
	height: 40px;
	padding: 0 30px;
	@include fontRR;
	font-size: 18px;
	line-height: 40px;
	color: $gray;
	font-style: italic;
	background-color: $lightGray;
	border: 1px solid $blue;
	cursor: pointer;
	&:disabled {
		border-color: $gray; }
	&__input {
		display: none; }
	&:before {
		content: '';
		position: absolute;
		top: -1px;
		right: -1px;
		width: 40px;
		height: 40px;
		background-color: $blue; }
	&:after {
		@include fontRR;
		content: '• • •';
		position: absolute;
		top: -1px;
		right: -1px;
		width: 40px;
		text-align: center;
		font-size: 20px;
		color: $white; }
	&.fill {
		font-style: normal;
		color: inherit;
		&.disabled {
			cursor: not-allowed;
			opacity: .5; }
		&.cert-file {
			padding: 0;
			line-height: auto;
			white-space: nowrap; } } }


.input {
	width: 100%;
	height: 40px;
	padding: 0 30px;
	@include fontRR;
	font-size: 18px;
	line-height: 40px;
	color: $black;
	background-color: $lightGray;
	border: 1px solid $blue;
	&:disabled {
		border-color: $gray;
		cursor: not-allowed;
		opacity: .5; }
	@include placeholder {
		@include fontRR;
		font-size: 18px;
		line-height: 40px;
		color: $gray;
		font-style: italic; }

	&--white {
		background-color: $white;
		border-color: $gray; }

	&--small {
		height: 30px;
		font-size: 16px;
		line-height: 30px;
		border-color: $blue;
		@include placeholder {
			font-size: 16px;
			line-height: 28px; }
		&[disabled] {
			border-color: $gray;
			opacity: 0.5; } } }

.input-rect {
	width: 60px;
	height: 60px;
	@include fontRB;
	font-size: 30px;
	line-height: 60px;
	text-align: center;
	background-color: $lightBlue;
	border: 1px solid $blue;
	@include placeholder {
		font-size: 18px; } }

.input-pass {
	position: relative;
	width: 100%;

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 40px;
		position: absolute;
		top: 50%;
		right: 5px;
		transform: translateY(-50%);
		cursor: pointer;
		&.is-show {
			&:before {
				content: '';
				position: absolute;
				transform: rotate(-45deg);
				width: 30px;
				height: 2px;
				background-color: #8aabff; } } } }

