.section {
	$this: &;
	box-shadow: 0px 4px 10px rgba($black, 0.25);
	border-radius: 0 0 5px 5px;

	&__title {
		height: 50px;
		line-height: 50px;
		padding: 0 30px;
		@include fontRM;
		font-size: 24px;
		color: $white;
		text-transform: uppercase;
		background-color: $blue;
		border-radius: 5px 5px 0 0; }

	&__subtitle {
		width: 230px;
		@include fontRM;
		font-size: 18px;
		text-transform: uppercase;
		color: $black;
		&--small {
			width: auto;
			margin: 0 30px; } }

	&__content {
		padding: 30px;
		background-color: $white;
		border-radius: 0 0 5px 5px; }

	&__row {
		display: flex;
		align-items: center;
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0; } }
	&__col {
		width: 50%;
		display: flex;
		align-items: center; }
	&__el {
		display: flex;
		flex-grow: 1; } }

