.auth {
	margin-bottom: 30px;

	&.disabled {
		opacity: .5;
		cursor: not-allowed;
		pointer-events: none; }
	&__switcher {
		border: 2px outset;
		background-color: $white;
		padding: 0 5px;
		cursor: pointer;

		&-active {
			border: 2px inset;
			background-color: $gray; }
		&-container {
			display: none;
			float: right;
			&.is-show {
				display: flex; } }
		&-label {
			color: $white;
			vertical-align: middle;
			margin: 0 5px; } }
	&__top-part-container {
		position: relative; }
	&__wait {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 4;
		background-color: $white;
		border-radius: 0 0 5px 5px;
		opacity: .4;
		&.is-show {
			display: block; } }
	&__loader {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 5;
		border-radius: 0 0 5px 5px;
		&.is-show {
			display: block; }
		.loader {
			@include vcenter;
			top: 30%;
			display: block;
			width: 100px;
			height: 100px; } }
	&__init {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 3;
		padding: 0 150px;
		background-color: $white;
		border-radius: 0 0 5px 5px;
		text-align: center;
		vertical-align: middle;
		&.is-show {
			display: flex;
			justify-content: space-around;
			align-items: center; } }
	&__crypto-button-init {
		height: 60px;
		width: 280px; } }

