.popup-help {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 30;
	line-height: 1.4;
	&.is-show {
		display: block; }

	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black,0.5);
		z-index: -1; }

	&__container {
		@include vcenter;
		top: 52%;
		width: 80vw;
		height: 80vh;
		padding-top: 50px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; }

	&__content {
		padding: 0 50px 50px;
		height: calc(100% - 60px);
		overflow-y: scroll; }

	&__title {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 50px 20px;
		background-color: $white;
		@include fontRB;
		font-size: 24px;
		color: $blue;
		text-align: center;
		z-index: 10;
		&-icon {
			display: flex;
			margin-right: 20px;
			margin-bottom: auto;
			img {
				width: 40px;
				height: 40px; } }
		&--black {
			color: $black; } }

	&__warning div {
		@include fontRB;
		text-align: center;
		width: 80%;
		margin: 0 auto 30px;
		border: 2px solid $blue;
		border-radius: 5px;
		padding: 20px; }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		z-index: 10;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }

	b {
		@include fontRB; }
	a:hover {
		text-decoration: underline; }
	.indent, ol li {
		text-indent: 50px; }

	ul {
		list-style: inside;
		li {
			padding-left: 70px; } }

	.mb {
		margin-bottom: 20px; }

	.blue {
		color: $blue; }

	.italic {
		font-style: italic; }

	.center {
		text-align: center; }

	.border {
		padding: 10px;
		border: 1px solid $black;
		color: $gray;
		background-color: $lightGray; }

	.close {
		position: relative;
		font-size: 0;
		display: inline-block;
		width: 30px;
		height: 10px;
		&:before, &:after {
			content: '';
			position: absolute;
			top: -20px;
			left: 15px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); } }
	.delete {
		position: relative;
		display: inline-block;
		top: -18px;
		right: -3px;
		width: 25px;
		height: 25px;
		margin-right: 5px;
		margin-bottom: -25px;
		border-radius: 50%;
		background-color: $blue;
		font-size: 0;
		transition: all 0.2s ease;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 11px;
			transform: rotate(45deg);
			width: 3px;
			height: 16px;
			background-color: $white; }
		&:after {
			transform: rotate(-45deg); }

		&--pink {
			background-color: $pink; } }

	.img {
		display: block;
		margin: 20px auto 0; } }
