.loader {
	display: none;
	border: 4px solid #f3f3f3;
	border-radius: 50%;
	border-top: 4px solid #3498db;
	width: 24px;
	height: 24px;
	animation: spin 2s linear infinite; }

@keyframes spin {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(360deg); } }


.preloader {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $white;
	&.is-show {
		display: block;
		.loader {
			border: 8px solid #f3f3f3;
			border-top: 8px solid #3498db;
			@include vcenter;
			top: 40%;
			display: block;
			width: 100px;
			height: 100px; } } }


.preloader-copyright {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $white;
	z-index: 1100;
	&.is-show {
		display: block; }
	.img {
		@include vcenter;
		top: 40%;
		width: 600px;
		height: 350px;
		background: url('../img/flag.png');
		background-size: contain;
		background-repeat: no-repeat;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 5px; }
		.text {
			@include vcenter;
			top: auto;
			bottom: 30px;
			color: $white;
			font-size: 18px; } } }
