.check {
	position: relative;
	padding-left: 30px;

	&__input {
		display: none; }

	&__label {
		cursor: pointer;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 4px;
			height: 16px;
			width: 16px;
			border: 2px solid $gray;
			border-radius: 3px; }

		&:after {
			display: none;
			content: '';
			position: absolute;
			left: 4px;
			top: 8px;
			height: 8px;
			width: 8px;
			background-color: $blue;
			border-radius: 2px; } }
	&.is-disabled {
		opacity: 0.5;
		.check__label {
			cursor: no-drop; } } }

.check__input:checked + .check__label:after {
	display: block; }

.check__input:checked + .check__label:before {
	border-color: $blue; }
