.popup-count-members {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 40;
    line-height: 1.4;
    &.is-show {
        display: block; }

    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($black,0.5);
        z-index: -1; }

    &__container {
        @include vcenter;
        top: 52%;
        width: 800px;
        height: auto;
        padding: 50px;
        background-color: $white;
        border: 2px solid $blue;
        border-radius: 5px; }


    &__btn {
        margin: 0 auto;
        width: 240px; }


    &__title {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 30px;
        background-color: $white;
        @include fontRB;
        text-transform: uppercase;
        font-size: 24px;
        color: $red;
        text-align: center;
        background-size: 30px;
        background-repeat: no-repeat; }

    &__row {
        margin-bottom: 20px;
        @include fontRM; }

    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        display: flex;
        margin-left: auto;
        font-size: 0;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $transp;
        z-index: 10;
        &:before, &:after {
            content: '';
            position: absolute;
            top: 5px;
            left: 19px;
            transform: rotate(45deg);
            width: 2px;
            height: 30px;
            background-color: $blue; }
        &:after {
            transform: rotate(-45deg); }
        &:hover {
            background-color: rgba($blue, 0.2); } } }
