.indiv-instructor,
.indiv-manager {

	&__btn-nav {
		justify-content: flex-start;
		font-size: 14px;
		margin-bottom: 10px;
		padding: 10px 40px;
		&:last-child {
			margin-bottom: 0; }
		&.is-active {
			background-color: $white;
			border: 1px solid $blue;
			color: $blue; } }

	&__btn-nav-text {
		width: 100%;
		display: flex;
		justify-content: space-between;
		& span:first-child {
			margin-right: 10px; } }

	&__header {
		position: relative;
		display: flex;
		justify-content: space-between;
		padding: 15px 30px 45px;

		&-item {
			display: flex;
			flex-direction: column;
			justify-content: space-around; }

		&-title {
			@include fontRB;
			font-size: 28px;
			color: $black; }
		&-subtitle {
			@include fontRB;
			font-size: 24px;
			color: $black; }

		&-text {
			margin-right: 20px;
			@include fontRM;
			color: $black; }

		&-btns {
			position: absolute;
			bottom: 1px;
			right: 0;
			display: flex; }
		&-btn {
			padding: 10px;
			margin-right: 1px;
			@include fontRM;
			font-size: 16px;
			color: $white;
			background-color: $blue;
			border: 1px solid $blue;
			border-radius: 5px;
			&.is-active {
				background-color: $white;
				color: $black; } } }


	&__table-row {
		display: grid;
		grid-template-columns: 10% 30% 15% 15% 15% 15%;
		border-bottom: 1px solid $blue;
		color: $black;
		&--all {
			background-color: $lightGray;
			border-bottom: 2px solid $blue;
			& div {
				@include fontRB;
				&:first-child {
					text-transform: uppercase; } } }
		&-header {
			background-color: $blue;
			color: $white;
			@include fontRM;
			text-transform: uppercase; }
		&--stationary {
			grid-template-columns: 40% 15% 15% 15% 15%; } }

	&__table-col {
		position: relative;
		padding: 15px 0;
		text-align: center;
		&:first-child {
			text-align: left;
			padding-left: 30px; }
		&-status {
			text-align: right;
			padding-right: 10px; }
		& .indiv-instructor__table-count {
			display: inline-block;
			width: 10%;
			text-align: center; }
		& .indiv-instructor__table-count:first-child {
			display: inline-block;
			width: 40%;
			text-align: right; }
		& .indiv-instructor__table-count:last-child {
			display: inline-block;
			width: 40%;
			text-align: left; }
		& .indiv-instructor__table-count:nth-child(2) {
			margin: 0 5%; } }


	&__table-status {
		position: absolute;
		top: 10%;
		left: 5%;
		width: 80%;
		height: 80%;
		border: 1px solid $blue;
		& span:first-child {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			background-color: $blueHover; }
		& span:last-child {
			position: absolute;
			@include vcenter;
			right: auto;
			left: auto;
			width: 45px;
			margin-left: 30px;
			text-align: left; } } }
