body {
	@include fontRR;
	font-size: 16px;
	line-height: 1.4;
	background-color: $lightGray;
	&.is-open-popup, &.is-finish {
		position: fixed;
		width: 100%; }
	&.is-finish {
		.finish {
			display: block; } }
	.finish {
		display: none; }
	&.android {
		background-color: $white; } }

.out.preloader-show {
	position: fixed; }

a {
	text-decoration: none; }

.container {
	width: 1140px;
	margin: 0 auto;
	position: relative; }

[hidden] {
	display: none; }

body.android .container {
	width: 100%;
	height: 100vh;
	padding: 40px 0 80px 0; }

.js-ver {
	position: fixed;
	right: 5px;
	top: 45px;
	font-size: 10px;
	color: $blue;
	opacity: 0.5;
	z-index: 1000; }

.cert {
	display: none;
	position: fixed;
	width: 100%;
	height: 100vh;
	z-index: 1000;
	top: 0;
	left: 0;
	background: white;
	&__content {
		@include vcenter;
		& div {
			margin-bottom: 20px; } } }


.select-dict {
  @include vcenter;
  z-index: 1001;
  width: 100vw;
  height: 100vh;
  background-color: #fff; }
